html, body, div {
  margin: 0;
  padding: 0;
}
html, body {
  height: 100%;
  font-family: Arial,"Microsoft YaHei","\5b8b\4f53";
  font-size: 14px;
}
body {
  background-color: #ececec;
}
.header {
  padding: 0 10px 0 60px;
  height: 40px;
  line-height: 40px;
  // background: url(../../image/logo.jpg) 10px center no-repeat;
  background-size: 40px auto;
  background-color: #333;

  a {
    margin: 0 5px;
    color: #ccc;
    text-decoration: none;

    &.active {
      color: #fff;
    }
  }
}

.main-wrapper {
  overflow: hidden;
  margin: 20px 0;
}
.menu-wrapper {
  width: 20%;
  float: left;
}
.main-container {
  float: left;
  width: 80%;
  padding: 0 20px;
  box-sizing: border-box;
}


